<template>
  <div id="Article" v-infinite-scroll="load">
    <article class="fadeIn" v-for="item in articleData" :key="item._id">
    <div class="one">
      <div class="oneImg" :style="{backgroundImage: `url(${baseURL}${item.cover})`}"></div>
      <div class="inside">
        <p class="tit"><span>{{ item.title }}</span>  <span>{{ new Date(item.date).getFullYear() }}-{{ new Date(item.date).getMonth() + 1 }}-{{ new Date(item.date).getDate()  }}</span> </p>
        <p class="insideContent">{{item.des}}</p>

        <div class="two">
          <span><i class="iconfont icon-redu"></i>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{ item.pv }}</span>
          <span><i class="iconfont icon-comment"></i></span>
          <router-link class="router" :to="`/article/${item._id}`">阅读全文</router-link>
        </div>
      </div>
    </div>
    </article>
  </div>

</template>

<script>

export default {
  name: "Article",
  data(){
    return{
      articleData:[],
      skip:0,
      AllArticleData:[],
    }
  },
  methods:{
    async getArticles(){
      let {data} = await  this.$axios({
        method:"get",
        url:"/get/article"
      })

      if (data.code) {return this.$message.error(data.message)}

      this.AllArticleData = data.data
      this.articleData.push(...this.AllArticleData.slice(this.skip,this.skip+5))

    },

    load(){
      if (!this.AllArticleData.length)return
      this.skip += 5
      this.articleData.push(...this.AllArticleData.slice(this.skip,this.skip+5))
    }
  },
  created() {
    this.getArticles()
  }

}
</script>

<style scoped lang="less">
#Article {
  article {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    margin-bottom: 15px;
    box-shadow: 0 0 4px #ddd;
    padding: 25px 20px;
    background-color: rgba(255, 255, 255, .7);
    min-width: 690px;

    .one {
      display: flex;

      .oneImg {
        height: 210px;
        width: 315px;
        box-sizing: border-box;
        padding-right: 1%;
        background-size: cover;
        background-position: 50%;
        border-radius: 30px;
      }

      .inside{
        flex: 1;
        margin-left: 35px;

        p.tit{

          :nth-child(1){
            font-size: 27px;
            font-weight: 700;
          }

          :nth-child(2){
            font-size: 22px;
            float: right;
            margin-right: 15px;
          }
        }

        p.insideContent{
          margin-top: 5px;
          display: inline-block;
          height: 130px;
          font-size: 16px;
          text-indent: 2em;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          white-space: pre-wrap;
          -webkit-line-clamp: 7;
          -webkit-box-orient: vertical;
        }

        .two{
          display: flex;
          text-align: center;
          align-items: center;

          span{
            flex: 1;

            i{
              font-size: 23px;
            }

          }

          .router{
            flex: 1.5;
            padding: 10px 20px;
            background-color: #409eff;
            color: #fff;
            transition: opacity .3s;
            vertical-align: 0px;

            &:hover {
              opacity: .8;
            }


          }

        }

      }




    }

    &.fadeIn {
      animation: fadeIn 1s 1;
      animation-fill-mode: backwards;
    }

    @keyframes fadeIn {
      from {
        transform: scale(.5);
        opacity: 0;
      }
      to {
        transform: scale(1);
        opacity: 1;
      }
    }
  }
}

</style>